/**
 * loader
 */
.spinner {
  width: 25px;
  height: 25px;
  line-height: 0;
}
.btn .spinner {
  width: 1em;
  height: 1em;
}
.spinner svg {
  filter: drop-shadow(0 0.1rem 0.2rem rgba(0, 0, 0, 0.1)) drop-shadow(0 0.2rem 0.4rem rgba(0, 0, 0, 0.25));
}
.spinner svg circle {
  fill: none;
  stroke-width: 30;
  transform-origin: center center;
}
.btn .spinner svg circle {
  stroke-width: 40;
}
.spinner svg:nth-child(1) circle {
  stroke: #7567f8;
  stroke-dashoffset: 628;
  transform: rotateY(0deg) rotateZ(-90deg);
  animation: anim-spinner 1000ms infinite cubic-bezier(0.14, 0.63, 0, 1);
}
.spinner svg:nth-child(2) {
  position: absolute;
  top: 0;
  left: 0;
}
.spinner svg:nth-child(2) circle {
  stroke: rgba(117, 103, 248, 0.05);
  stroke-dashoffset: 0;
}
@keyframes anim-spinner {
  0% {
    stroke-dashoffset: 628;
    transform: rotateY(0deg) rotateZ(-90deg);
    transform-origin: 48% 48%;
  }
  50% {
    stroke-dashoffset: 0;
    transform: rotateY(0deg) rotateZ(90deg);
    animation-timing-function: cubic-bezier(0.14, 0.63, 0, 1);
    transform-origin: 48% 48%;
  }
  50.0001% {
    stroke-dashoffset: 0;
    transform: rotateY(180deg) rotateZ(90deg);
    transform-origin: center center;
  }
  100% {
    stroke-dashoffset: 628;
    transform: rotateY(180deg) rotateZ(-90deg);
    transform-origin: 46% 50%;
  }
}
.loader--x .filler span:nth-child(1) {
  width: 0;
  background: linear-gradient(90deg, #7567f8 33%, #5f4ff7 100%);
  box-shadow: 0 0.1rem 1rem rgba(95, 79, 247, 0.2), 0 0.2rem 2rem rgba(95, 79, 247, 0.4);
  animation: anim-loader--x 1000ms infinite cubic-bezier(0.14, 0.63, 0, 1);
}
.loader--x .filler span:nth-child(2) {
  background: rgba(117, 103, 248, 0.05);
}
.loader--y .filler span:nth-child(1) {
  height: 0;
  background: linear-gradient(0deg, #7567f8 33%, #5f4ff7 100%);
  box-shadow: 0 0.1rem 1rem rgba(95, 79, 247, 0.2), 0 0.2rem 2rem rgba(95, 79, 247, 0.4);
  animation: anim-loader--y 1000ms infinite cubic-bezier(0.14, 0.63, 0, 1);
}
.loader--y .filler span:nth-child(2) {
  background: rgba(117, 103, 248, 0.05);
}
@keyframes anim-loader--x {
  0% {
    left: 0;
    width: 0;
  }
  50% {
    left: 0;
    width: 100%;
    animation-timing-function: cubic-bezier(0.14, 0.63, 0, 1);
  }
  100% {
    left: 100%;
    width: 0;
  }
}
@keyframes anim-loader--y {
  0% {
    top: 100%;
    height: 0;
  }
  50% {
    top: 0;
    height: 100%;
    animation-timing-function: cubic-bezier(0.14, 0.63, 0, 1);
  }
  100% {
    top: 0;
    height: 0;
  }
}
.loader-size--x .filler,
.loader-size--top .filler,
.loader-size--bottom .filler {
  height: 3px;
}
.loader-size--y .filler,
.loader-size--left .filler,
.loader-size--right .filler {
  width: 3px;
}
.loader-size--x .filler {
  left: 25%;
  right: 25%;
}
.loader-size--y .filler {
  top: 25%;
  bottom: 25%;
}
.loader--background {
  background: rgba(255, 255, 255, 0.8);
}
.loader.active {
  opacity: 1;
  transition-property: opacity;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.14, 0.63, 0, 1);
}
.loader.active .spinner,
.loader.active .filler {
  opacity: 1;
  transform: scale(1);
  transition-property: opacity, transform;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.14, 0.63, 0, 1);
}
.loader.out {
  opacity: 0;
  transition-property: opacity;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.14, 0.63, 0, 1);
}
.loader.out .spinner,
.loader.out .filler {
  opacity: 0;
  transform: scale(0.75, 0.75);
  transition-property: opacity, transform;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.14, 0.63, 0, 1);
}
.loader--mouse {
  opacity: 0;
  transition-property: opacity;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.14, 0.63, 0, 1);
}
