    animation-timing-function: @bez-out;
    animation-fill-mode: forwards;
  }
}

@keyframes anim-y--in {
  0% {
    opacity: 0;
    transform: translateY(-1.5rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
