// order

.order(@i, @breakpoint: none) when (@breakpoint = none) {
  @val: extract(@orders, @i + 1);
  order: @val;
}

.order(@i, @breakpoint: none) when (default()) {
  @val: extract(@orders, @i + 1);
  @query-min: 'min-@{breakpoint}';
  @media @@query-min {
    order: @val;
  }
}

// generate

& when not (@generate-util = false) {

  // generate-utils

  .generate-utils();
