@import '~xtend-library/src/core/drop/_drop.less';

/**
 * vars
 */

// space

@drop-space: 6px; // space between button and drop

/**
 * drop
 */

.drop {
  > .drop-inner {
    border-radius: .5em;
    > .drop-content {
      > .list, > .list-block {
        .padding(y, 1.3rem); // vertical space
        min-width: 18rem;
      }
    }
    > .drop-design, > .drop-content {
      border-width: 1px;
      border-style: solid;
      border-color: transparent;
    }
  }
  // default options
  .drop--left();
  .drop--bottom();
}

// variant

.drop--default {
  > .drop-inner {
    color: @text;
    > .drop-design {
      border-color: @gray-300;
      background: @white;
      box-shadow: @shadow-light;
    }
  }
  > .drop-inner > .drop-content > .list--drop {
    .list-btns({ .btn--text() });
  }
}

.drop--primary {
  > .drop-inner {
    .text-inverse();
    > .drop-design {
      border-color: @accent--dark;
      background: @accent;
      box-shadow: @shadow-dark;
    }
  }
  > .drop-inner > .drop-content > .list--drop {
    .list-btns({ .btn--text() });
  }
}

// special

.drop--squared {
  > .drop-inner {
    border-radius: 0;
  }
}

.drop--noborder {
  > .drop-inner {
    > .drop-design {
      border: 0;
    }
  }
}

// special

.drop--disable {
  // disable custom animation
  > .drop > .drop-inner {
    > .drop-content {
      opacity: 1 !important;
      .trans-anim-none() !important;
    }
  }
}

.drop--overflow-y {
  > .drop-inner > .drop-content {
    overflow-y: auto;
    max-height: 25rem;
    .overflow-style(@mode: inner);
  }
}

// animation

.drop-container {
  &, > .drop {
    animation-duration: @time-medium + @time-small; // needed for .out animation timing, put max animation time
  }
  &.active {
    > .drop {
      > .drop-inner {
        > .drop-design {
          .anim-zoom.active();
        }
        > .drop-content {
          .anim-y.active();
          animation-delay: @time-small;
        }
      }
      > .backdrop {
        .backdrop.active();
      }
    }
  }
  &.out {
    > .drop {
      > .drop-inner {
        > .drop-design {
          .anim-zoom.out();
          animation-delay: @time-small;
        }
        > .drop-content {
          .anim-y.out();
        }
      }
      > .backdrop {
        .backdrop.out();
      }
    }
  }
}
