@import '~xtend-library/src/core/badge/_badge.less';

/**
 * badge
 */

.badge {
  border-radius: 0;
  border-width: 1px;
  border-style: solid;
  border-color: transparent;
  .font-sans--semibold();
  line-height: 1.4;
  letter-spacing: .025em;
  text-transform: uppercase;
  // default options
  .badge--medium();
}

// variant

.badge--default {
  border-color: @gray-300;
  background: @gray-300;
  color: @text;
}

.badge--primary {
  border-color: @accent;
  background: @accent;
  .text-inverse();
}

.badge--success {
  border-color: @success;
  background: @success;
  .text-inverse();
}

.badge--error {
  border-color: @error;
  background: @error;
  .text-inverse();
}

// size

.badge--small {
  font-size: 11px;
  padding: .3rem .8rem;
}

.badge--medium {
  font-size: 13px;
  padding: .4rem .9rem;
}

.badge--big {
  font-size: 15px;
  padding: .5rem 1rem;
}

// special

.badge--squared {
  border-radius: 0;
}
